import RestRequest from '../BaseCrossProject/RestSharp/RestRequest';
import ClientApi from '../BaseCrossProject/RestSharp/ClientApi';
import AuthApi from '../BaseCrossProject/RestSharp/AuthApi';
import { ParameterType } from '../BaseCrossProject/RestSharp/Enums';

export default class Prontuario {
	private baseUrlApi: string;
	private clientApi: ClientApi;

	constructor(baseUrlApi: string) {
		this.baseUrlApi = baseUrlApi.endsWith("/") ? baseUrlApi : `${baseUrlApi}/`;
		this.clientApi = new ClientApi(this.baseUrlApi);
	}

	public async Autenticar(login: string, password: string): Promise<any> {
		const restRequest = new RestRequest("Security/Authenticate")
			.AddOrUpdateParameter("email", login, ParameterType.QueryString)
			.AddOrUpdateParameter("password", password, ParameterType.QueryString);

		const authApi = new AuthApi(this.baseUrlApi, restRequest, 10);
		this.clientApi = new ClientApi(this.baseUrlApi, authApi);
		return authApi.RenewToken();
	}

	public async QuemEstaLogado(): Promise<any> {
		const restRequest = new RestRequest("Test/Logado")
		return this.clientApi.Send(restRequest)
			.then(r => r.json());
	}
}
