import React, { InputHTMLAttributes } from 'react';
import './styles.css'

interface InputProps extends InputHTMLAttributes<HTMLInputElement> {
	label: string;
	change(value: string): any;
}

const Input: React.FC<InputProps> = (props) => {
	//const { ...rest } = props;
	return (
		<div className="input-block">
			<label id={props.name} htmlFor={props.name}>{props.label}</label>
			<input type={props.type ?? "text"} name={props.name} id={props.name} value={props.value} onChange={e => props.change(e.target.value)} />
		</div>
	);
}

export default Input;