
export default class Drag {
	private state: WebKitPoint;
	private titleWindow: HTMLElement;

	private constructor(titleBar: HTMLElement) {
		this.state = { x: 0, y: 0 };
		this.titleWindow = titleBar.parentElement ?? titleBar;
		titleBar.onmousedown = (e: MouseEvent) => this.dragMouseDown(e);
	}

	private dragMouseDown(e: MouseEvent): void {
		e = e || window.event;
		e.preventDefault();
		this.state.x = e.clientX;
		this.state.y = e.clientY;
		document.onmouseup = (e1: MouseEvent) => this.closeElementDrag(e1);
		document.onmousemove = (e1: MouseEvent) => this.elementDrag(e1);
	}

	private elementDrag(e: MouseEvent): void {
		e = e || window.event;
		e.preventDefault();
		let x = this.state.x - e.clientX;
		let y = this.state.y - e.clientY;
		this.state.x = e.clientX;
		this.state.y = e.clientY;
		this.titleWindow.style.left = (this.titleWindow.offsetLeft - x) + "px";
		this.titleWindow.style.top = (this.titleWindow.offsetTop - y) + "px";
	}

	private closeElementDrag(e: MouseEvent): void {
		document.onmouseup = null;
		document.onmousemove = null;
	}

	public static Init(idOrQuerySelector: string, isQuerySelector: boolean = false): Drag {
		const element: any = isQuerySelector
			? document.getElementsByClassName(idOrQuerySelector)
			: document.getElementById(idOrQuerySelector);

		return new Drag(element);
	}
}
