import Parameter from "./Parameter";
import { Method, ParameterType } from "./Enums";

export default class RestRequest {
	private Parameters: Parameter[];
	private Resource: string;
	private method: Method;
	private ContentType: string;
	private body?: string | null;

	constructor(resource: string, method: Method = Method.GET, contentType: string = "text/json") {
		this.Parameters = [];
		this.Resource = resource;
		this.method = method;
		this.ContentType = contentType;
		this.body = null;
	}

	public GetUri(baseUri: string): string {
		return baseUri + this.Resource + this.QueryString;
	}

	public GetRequest(): RequestInit {
		return {
			headers: this.Headers,
			method: this.Method,
			body: this.Body
		}
	}

	public ResetParameters(): void {
		this.Parameters = [];
	}

	public AddOrUpdateParameter(name: string, value: string, type?: ParameterType): RestRequest {
		let param = this.Where(name, type);
		if (param.length > 0) {
			param[0].Value = value;
		}
		else {
			this.Parameters.push(new Parameter(name, value, type ?? ParameterType.QueryString));
		}
		return this;
	}

	get Body(): string | null {
		return (this.body && this.body.length > 0) ? JSON.stringify(this.body) : null;
	}

	get Method(): string {
		const m = this.method;
		return Method[m];
	}

	get Headers(): Record<string, string> {
		const header = {};
		Reflect.set(this, "Content-Type", this.ContentType, header);
		Reflect.set(this, "Accept", "application/json, text/json, text/plain, */*", header)
		Reflect.set(this, "cache-control", "no-cache", header)
		const params = this.Where(undefined, ParameterType.HttpHeader);
		params.forEach(item => Reflect.set(this, item.Name, item.Value, header));
		return header;
	}

	get QueryString(): string {
		const params = this.Where(undefined, ParameterType.QueryString);
		const query = params.map(item => `${item.Name}=${item.Value}`).join('&');
		return (params.length > 0) ? "?" + query : "";
	}

	FirstOrDefault(name?: string, type?: ParameterType): string {
		const param = this.Where(name, type);
		return (param.length > 0) ? param[0].Value : "";
	}

	RemoveParameter(name?: string, type?: ParameterType): RestRequest {
		const param = this.Where(name, type);
		while (param.length > 0) {
			const index = this.Parameters.indexOf(param[0]);
			this.Parameters.splice(index, 1);
			param.splice(0, 1);
		}
		return this;
	}

	private Where(name?: string, type?: ParameterType): Parameter[] {
		let params = this.Parameters;
		if (name)
			params = params.filter((value, index, array) => value.Name === name);
		if (type)
			params = params.filter((value, index, array) => value.Type === type);

		return params;
	}
}
