import React, { useState } from 'react';
import { useHistory } from "react-router-dom";

import Api from '../../index';
import Input from '../../components/Input';
import TitleWindow from '../../components/TitleWindow';

import './styles.css'


export default function Login() {
	const [login, setLogin] = useState("joao@gmail.com");
	const [password, setPassword] = useState('');
	const history = useHistory();

	async function handleSubmit(e: React.MouseEvent<HTMLButtonElement, MouseEvent>) {
		e.preventDefault();

		await Api.Autenticar(login, password)
			.then(a => history.push("/"))
			.catch(e => alert("Não foi possível fazer o Login: " + e.message));
	}

	return (
		<TitleWindow label="Prontuário - Autenticação" style={{ width: "25vw" }} >
			<Input type="text" label="login" name="login" value={login} change={setLogin} required />
			<Input type="password" label="password" name="password" value={password} change={setPassword} />

			<div className="input-block">
				<button type="button" onClick={handleSubmit}>Login</button>
			</div>
		</TitleWindow>
	);
}
