
export interface Coordenadas { top: string, left: string, width: string, height: string }
export enum WindowState { Minimizado = 0, Normal = 1, Maximizado = 2 }

export default class ControlBox {
	private coordenadas: Coordenadas = { top: "0", left: "0", width: "0", height: "0" };
	private twState: WindowState = WindowState.Normal;
	private root: HTMLElement = document.createElement("root");
	private main: HTMLElement = document.createElement("main");

	private constructor(uniqueId: string) {
		this.root = document.getElementById(`title_window_${uniqueId}`) ?? this.root;
		this.main = document.getElementById(`title_window_main_${uniqueId}`) ?? this.main;
		document.getElementById(`twhbb_help_${uniqueId}`)?.addEventListener("click", e => alert("Help indispoível!"));
		document.getElementById(`twhbb_mini_${uniqueId}`)?.addEventListener("click", e => this.min_res_max(-1));
		document.getElementById(`twhbb_rest_${uniqueId}`)?.addEventListener("click", e => this.min_res_max(+1));
		document.getElementById(`twhbb_clos_${uniqueId}`)?.addEventListener("click", e => alert("Close indispoível!"));
	}

	private min_res_max(direction: number): void {
		if (this.twState === WindowState.Minimizado) {
			set(this.root.style, this.coordenadas);
			this.twState++;
			this.main.style.display = "";

		} else if (this.twState === WindowState.Normal) {
			set(this.coordenadas, this.root.style);
			this.twState = this.twState + direction;

			if (this.twState === 0) { // Min
				this.main.style.display = "none";
			} else { // Max
				set(this.root.style, { top: "2vh", left: "2vw", width: "96vw", height: "96vh" });
				this.main.style.display = "";
			}

		} else if (this.twState === WindowState.Maximizado) {
			set(this.root.style, this.coordenadas);
			this.twState--;
		}

		function set(obj1: any, obj2: any): void {
			obj1.top = obj2.top;
			obj1.left = obj2.left;
			obj1.width = obj2.width;
			obj1.height = obj2.height;
		}
	}

	public static Init(uniqueId: string): ControlBox {
		return new ControlBox(uniqueId);
	}
}
