import DateTime from '../DateTime';

export class Usuario {
	constructor(public readonly Nome: string, public readonly Email: string, public readonly Celular: string, public readonly Roles: string) { }

	public static New(usuario: any): Usuario {
		try {
			return new Usuario(usuario.nome, usuario.email, usuario.celular, usuario.roles)
		} catch (error) {
			return Usuario.Anonimo;
		}
	}

	public static readonly Anonimo: Usuario = new Usuario("Anonimo", "anonimo@anonimo.com.br", "(xx) x xxxx-xxxx", "Anonimo");
}

export default class JwtAuthorization {
	constructor(public readonly Scheme: string, public readonly Token: string, public readonly Expires: Date, public readonly Usuario: Usuario) { }

	public static New(response: any): JwtAuthorization {
		try {
			return new JwtAuthorization(response.scheme, response.token, DateTime.Parse(response.expires), Usuario.New(response.usuario));
		} catch (error) {
			return JwtAuthorization.Invalid(error);
		}
	}

	public static Invalid(error: any): JwtAuthorization { return new JwtAuthorization("Error", String(error), DateTime.UtcNow, Usuario.Anonimo); }
}
