
export default class DateTime {
	public static get Now(): Date {
		return new Date();
	}

	public static get UtcNow(): Date {
		const now = DateTime.Now;
		return DateTime.ToUTC(now);
	}

	public static ToUTC(date: Date): Date {
		const utcDate = Date.UTC(
			date.getUTCFullYear(),
			date.getUTCMonth(),
			date.getUTCDate(),
			date.getUTCHours(),
			date.getUTCMinutes(),
			date.getUTCSeconds()
		);
		return new Date(utcDate);
	}

	public static Parse(date: any): Date {
		return ((date === undefined) || (date === null)) ? DateTime.UtcNow : new Date(date);
	}
}
