import RestRequest from "./RestRequest";

export default class AbstractApi {
	private _baseUrlApi: string;

	constructor(baseUrlApi: string) {
		this._baseUrlApi = baseUrlApi;
	}

	protected async SendImpl(restRequest: RestRequest): Promise<Response> {
		const fullUrl = restRequest.GetUri(this._baseUrlApi);
		const request = restRequest.GetRequest();

		return fetch(fullUrl, request)
	}
}
