import React from 'react';

import logoImg from "../../assets/images/logo.svg";
import landingImg from "../../assets/images/landing.svg";
import purpleHeartIcon from "../../assets/images/icons/purple-heart.svg";

import './styles.css';
import Api from '../..';

export default function Landing() {

	async function handleClick(event: React.MouseEvent<HTMLButtonElement, MouseEvent>) {
		event.preventDefault();

		await Api.QuemEstaLogado()
			.then(r => alert(r.message))
			.catch(r => alert("você não está autenticado: " + r?.message));
	}

	return (
		<div id="page-landing" >
			<div id="page-landing-content" className="container">

				<div className="logo-container">
					<img src={logoImg} alt="Meu Prontuario" />
					<h2>Sua plataforma de Prontuário Médico Eletrônico</h2>
				</div>

				<img src={landingImg} alt="Meu Prontuario" className="hero-image" />

				<div className="buttons-container">
					<button type="button" onClick={handleClick}>Quem está logado ?</button>
				</div>

				<span className="total-connections">
					Total de 200 conexões já realizadas
						<img src={purpleHeartIcon} alt="Coração Roxo" />
				</span>
			</div>
		</div>
	);
}


