import AbstractApi from './AbstractApi';
import RestRequest from './RestRequest';
import JwtAuthorization from './JwtAuthorization';
import { ParameterType } from './Enums';
import DateTime from '../DateTime';

export default class AuthApi extends AbstractApi {
	private _restRequestGetToken: RestRequest;
	private _sessionTimeOutInMillis: Number;
	private _jwtAuthorization: JwtAuthorization;
	private _lastActivity: Date;

	constructor(baseUrlApi: string, restRequestGetToken: RestRequest, sessionTimeOutInMinutes: number) {
		super(baseUrlApi);
		this._restRequestGetToken = restRequestGetToken;
		this._sessionTimeOutInMillis = sessionTimeOutInMinutes * 60 * 1000;
		this._jwtAuthorization = JwtAuthorization.Invalid("Empty");
		this._lastActivity = DateTime.UtcNow;
		this.ResetTimerOfTimeOut();
	}

	private get Token(): string {
		return `${this._jwtAuthorization?.Scheme} ${this._jwtAuthorization?.Token}`;
	}

	public get IsValid(): boolean {
		return (this._jwtAuthorization != null) && !this.IsExpired;
	}

	private get IsExpired(): boolean {
		return DateTime.UtcNow.getTime() > this._jwtAuthorization.Expires.getTime();
	}

	public get IsClientIdle(): boolean {
		const time = (DateTime.UtcNow.getTime() - this._lastActivity.getTime());
		return time > this._sessionTimeOutInMillis;
	}

	private ResetTimerOfTimeOut(): void {
		this._lastActivity = DateTime.UtcNow;
	}

	private ResetParametersOfRequestGetToken(): void {
		this._restRequestGetToken.ResetParameters();
	}

	public async TryUpdateTokenInRequest(restRequest: RestRequest): Promise<void> {
		if (await this.GetValidToken()) {
			restRequest.AddOrUpdateParameter("Authorization", this.Token, ParameterType.HttpHeader);
		}
		else {
			restRequest.RemoveParameter("Authorization", ParameterType.HttpHeader);
		}
	}

	private async GetValidToken(): Promise<boolean> {
		if (this.IsClientIdle) {
			this.ResetParametersOfRequestGetToken();
			return false;
		}
		this.ResetTimerOfTimeOut();
		return this.IsValid || await this.TokenRenewed();
	}

	private async TokenRenewed(): Promise<boolean> {
		await this.RenewToken();
		return this.IsValid;
	}

	public async RenewToken1(): Promise<JwtAuthorization> {
		const response = await this.Authenticate();
		this._jwtAuthorization = JwtAuthorization.New(response);
		return this._jwtAuthorization;
	}

	public async RenewToken(): Promise<JwtAuthorization> {
		return this.Authenticate()
			.then(json => JwtAuthorization.New(json))
			.then(jwta => this._jwtAuthorization = jwta)
			.then(this.ValidateJWT)
	}

	private async Authenticate(): Promise<any> {
		return super.SendImpl(this._restRequestGetToken)
			.then(response => response.json());
	}

	private ValidateJWT(jwta: JwtAuthorization): JwtAuthorization {
		if (jwta.Expires > DateTime.UtcNow) {
			return jwta;
		}
		throw new Error("Token Expirado");
	}
}
