import AbstractApi from "./AbstractApi";
import AuthApi from "./AuthApi";
import RestRequest from "./RestRequest";

export default class ClientApi extends AbstractApi {
	private _authApi?: AuthApi;

	constructor(baseUrlApi: string, authApi?: AuthApi) {
		super(baseUrlApi);
		this._authApi = authApi;
	}

	public async Send(restRequest: RestRequest): Promise<Response> {
		if (this._authApi) {
			await this._authApi.TryUpdateTokenInRequest(restRequest);
		}
		return super.SendImpl(restRequest).then(this.ValidateRespose);
	}

	protected async ValidateRespose(response: Response): Promise<Response> {
		if (response.ok) {
			return response;
		}
		throw new Error(await response.text());
	}
}
