import React, { FormEvent } from 'react';
import DateTime from '../../BaseCrossProject/DateTime';
import ControlBox from './ControlBox';
import Drag from './Drag';
import './styles.css'

interface TitleWindowProps {
	label: string;
	style?: React.CSSProperties;
	footer?: React.ReactNode;
	onSubmit?: ((event: FormEvent<HTMLFormElement>) => void);
}

export default class TitleWindow extends React.Component<TitleWindowProps> {
	private uniqueId: string;

	constructor(props: any) {
		super(props);
		this.uniqueId = DateTime.Now.getTime().toString();
	}

	componentDidMount() {
		Drag.Init(`title_window_header_${this.uniqueId}`);
		ControlBox.Init(this.uniqueId);
	}

	render() {
		return (
			<div id={`title_window_${this.uniqueId}`} className="title-window" style={this.props.style}>
				<header id={`title_window_header_${this.uniqueId}`} className="title-window-header">
					<label className="title-window-header-label">{this.props.label}</label>
					<div className="title-window-header-buttons">
						<button id={`twhbb_help_${this.uniqueId}`} className="title-window-header-buttons-button-help"    >?</button>
						<button id={`twhbb_mini_${this.uniqueId}`} className="title-window-header-buttons-button-minimize">-</button>
						<button id={`twhbb_rest_${this.uniqueId}`} className="title-window-header-buttons-button-restore" >+</button>
						<button id={`twhbb_clos_${this.uniqueId}`} className="title-window-header-buttons-button-close" > X</button >
					</div >
				</header >
				<main id={`title_window_main_${this.uniqueId}`} className="title-window-main">
					<form className="title-window-main-form" onSubmit={this.props.onSubmit}>
						{this.props.children}
					</form>
				</main>
				<footer className="title-window-footer">
					{this.props.footer}
				</footer>
			</div >
		);
	}
}
